import { Select, Input, Button, DatePicker, Form } from "antd";
import { useEffect, useState } from "react";
import moment from 'moment';


export default function LocationInfo({ currentLocation }) {

  const [form] = Form.useForm()

  const onFinish = (values) => {
    console.log('Success:', values);
  };
  const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo);
  };


  useEffect(() => {
    form.setFieldsValue({
      type: currentLocation?.type,
      name: currentLocation?.name || "",
      quantity: currentLocation?.quantity || 0,
      expire: moment(currentLocation.expire),
      distance:currentLocation?.distance
    })
  }, [currentLocation])


  return (
    <Form

      form={form}
      name="basic"
      labelCol={{
        span: 8,
      }}
      wrapperCol={{
        span: 16,
      }}

      style={{
        minWidth: "30%",
        minHeight: 200
      }}

      onFinish={onFinish}
      onFinishFailed={onFinishFailed}
      autoComplete="off"
      disabled={true}
    >
      <div style={{ textAlign: 'center', fontSize: 40, fontWeight: 1200 }}>
        Thng tin location hin ti
      </div>

      <Form.Item
        label="Loi hng"
        name="type"
      >
        <Select
          showSearch
          optionFilterProp="children"
          options={[
            {
              value: "A",
              label: "A",
            },
            {
              value: "B",
              label: "B",
            },
            {
              value: "C",
              label: "C",
            },
          ]}
        />
      </Form.Item>

      <Form.Item
        label="S lng"
        name="quantity"
      >
        <Input />
      </Form.Item>

      <Form.Item
        label="Tn"
        name="name"
      >
        <Input />
      </Form.Item>

      <Form.Item
        label="Khong cch"
        name="distance"
      >
        <Input />
      </Form.Item>

      {/*<Form.Item
        label="Ht hn"
        name="expire"
      >
        <DatePicker  />
        </Form.Item> */}
    </Form>
  );
}
